.container {
    margin-left: auto;
    margin-right: auto;
    min-width: 320px;
    padding-left: 20px;
    padding-right: 20px;
    width: 100vw;
    @media screen and (min-width: 480px) {
        width: 480px;
    }
    @media screen and (min-width: 768px) {
        width: 768px;
        padding-left: 34px;
        padding-right: 34px;
    }
    @media screen and (min-width: 1200px) {
        width: 1200px;
        padding-left: 115px;
        padding-right: 115px;
    }
}